.days-of-week,
.disabled {
    text-align: center;
    margin: 20px;
    color: #c9c9d2;
    color: #f7fbea;
    width: 30px;
    display: inline-block;
    padding: 6px;
    font-size: 18px;
}
.days-of-week:hover {
    cursor: pointer;
    font-weight: bold;
}
.selected {
    background-color: #428cc9;
    background-color: white;
    color: #020c2c;
    border-radius: 4px;
}
.login-btn {
    background-color: #f1f1e6;
    border-radius: 4px;
    border: none;
    padding: 8px;
    margin-right: 16px;
}
.login-btn:hover {
    cursor: pointer;
}
.make-appoint-btn {
    background-color: #f1f1e6;
    border-radius: 4px;
    border: none;
    padding: 8px;
    margin-left: 16px;
}
.make-appoint-btn:hover {
    cursor: pointer;
}
#root {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.date-nav {
    display: flex;
    justify-content: space-between;
    background-color: #020c2c;
    padding: 16px;
    border-radius: 8px;
    margin-top: 150px;
}
.date-nav button {
    background-color: #b9baba;
    border: none;
    border-radius: 4px;
}
.month-days {
    border: 2px solid #020c2c;
    border-radius: 8px;
    margin-bottom: 16px;
}
.date-nav button:hover {
    cursor: pointer;
}
.date-nav button:focus {
    outline: 0;
}
.date-title {
    font-weight: bold;
    font-size: 26px;
    display: inline-block;
    width: 300px;
    text-align: center;
    color: #b9baba;
}
.appointment-input {
    width: 60%;
    height: 30px;
    margin-bottom: 32px;
    background-color: #f1f1e6;
    border: none;
    padding: 4px;
    border-radius: 4px;
}
.nav-bar {
    background: #2e2a50;
    position: fixed;
    top: -1px;
    left: 0;
    right: 0;
}
.nav-bar ul {
    display: flex;
    justify-content: space-between;
}
.nav-bar h1 {
    color: #ebeeff;
    margin: 0;
}
.nav-bar li {
    list-style: none;
    display: inline-block;
}
.appointments h2 {
    color: #ffefca;
}
.appointments p {
    color: #eaeeff;
}

